import { Component, OnInit } from '@angular/core';
import { trigger, style, transition, animate, keyframes, query, stagger } from '@angular/animations'
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],
  animations: [

    trigger('goalCnt', [
      transition('* =>*', [
        query(':enter', style({ opacity: 0 }), { optional: true }),

        query(':enter', stagger('300ms', [
          animate('.6s ease-in', keyframes([
            style({ opacity: 0, transform: 'translateY(-100%)', offset: 0 }),
            style({ opacity: .5, transform: 'translateY(-20%)', offset: .3 }),
            style({ opacity: 1, transform: 'translateY(-75%)', offset: 1 })
          ]))]), { optional: true }),

        query(':leave', stagger('300ms', [
          animate('.6s ease-in', keyframes([
            style({ opacity: 1, transform: 'translateY(0)', offset: 0 }),
            style({ opacity: .5, transform: 'translateY(35%)', offset: .3 }),
            style({ opacity: 0, transform: 'translateY(-75%)', offset: 1 })
          ]))]), { optional: true })


      ])
    ])



  ]

})
export class HomeComponent implements OnInit {


  itemCount: Number = 0
  btnSubmit: String = 'Add Item'
  goalText: String = 'Learn Anguler 5'

  goalCnt = []

  constructor() { }

  ngOnInit() {
    this.itemCount = this.goalCnt.length;
  }


  addItem() {
    this.goalCnt.push(this.goalText)
    this.goalText = ""
    this.itemCount = this.goalCnt.length;
  }

  removeItem() {
    this.goalCnt.pop();
    this.itemCount = this.goalCnt.length;
  }

}



